:root{
  --gradient1: #34F8EF;
  --gradient2: #2EB3D9;
  --cardcolorlow: #0f404e90;
  --cardcolor: #0f404e;
  --textcolor: #F1F1F1;
}

.app{
  background: linear-gradient(45deg, var(--gradient1) 30%, var(--gradient2) 90%);
  min-height: 100vh;
  width: 100vw;
  display: grid;
  place-items: center;
  text-align: center;
}

@keyframes showup {
  0% {
    scale: 0.5;
    opacity: 0.1;
  }
  80% {
    scale: 1.2;
    opacity: 0.5;

  }
  100% {
    scale: 1;
    opacity: 1;
  }
  
}

.main{
  animation: showup 0.220s forwards;
  min-width: 300px;
  max-width: 70vw;
  border-radius: 20px;
  margin: 20px 0;
  padding: 10px;
  min-height: 30vh;

  background-color: var(--cardcolorlow);
  color: var(--textcolor);
}


a{color:var(--textcolor); text-decoration: none;}

.intro{
 display: grid;
  place-items: center;

  font-size: 1rem;
  font-weight: 500;
  padding-top: 20px;
  text-align: center;
}

.intro > p{
  max-width: 200px;
}
#title{
  font-size: 2rem;
  font-weight: 800;

}
#desc{
  margin-top: -20px;
}


.linktab{
  display: flex;
  justify-content: space-around;
  margin-top: 0.5rem;
}

.link{
  animation: showup 0.400s forwards;
  min-width: 200px;
  max-width: 300px;
  padding: 0.5rem;
  border-radius: 10px;
  background-color: var(--cardcolor);
  margin: 1.5rem 0;

}

.link > a{
  font-size: 1.3rem;

}

.socials{
  display: flex;
  flex-direction: row;
  color: var(--textcolor);
  justify-content: space-around;
  margin-top: 10px;
  padding-top: 20px;
  margin-bottom: 10px;

}

.link:nth-child(1){
  background-color: var(--textcolor);

}
.link:nth-child(1)> a{
color:var(--cardcolor) ;


}


.circle{
  border-radius: 50%;
  object-fit: cover;
  width: 200px;
  height: 200px;
  
}

.square{
  object-fit: cover;
  width: 200px;
  height: 200px;
}



.logo{
  animation: showup 0.400s forwards;
}

@media screen and (min-width: 601px) {
  .main{
    display: flex;
    flex-direction: row;
    padding: 0 40px;

  }

  .intro{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    height: 400px;
  }

  .linktab{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 20px;
    height: 400px;
  }

  
}

@keyframes textshowup {
  40% {
    opacity: 0.3;

  }
  100% {
    opacity: 1;
  }
  
}

.intro > p{
  animation: textshowup 0.200s forwards;
}

